:root{
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.75rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
  --max-width: 1170px;
  --fixed-width: 450px;
  --clr-orange-1: hsl(12, 83%, 98%);
  --clr-orange-2: hsl(14, 91%, 95%);
  --clr-orange-3: hsl(12, 89%, 89%);
  --clr-orange-4: hsl(13, 87%, 82%);
  --clr-orange-5: hsl(13, 88%, 68%);
  --clr-orange-6: hsl(13, 88%, 55%);
  --clr-orange-7: hsl(13, 74%, 49%);
  --clr-orange-8: hsl(13, 74%, 33%);
  --clr-orange-9: hsl(13, 73%, 25%);
  --clr-orange-10: hsl(13, 73%, 16%);
}

html {
  height:100%;
}

body {
  background: rgb(245,197,46);
  background: linear-gradient(180deg, rgba(245,197,46,1) 0%, rgba(247,231,181,1) 50%, rgba(245,197,46,1) 100%);
}

.icon {
  display: flex;
  /* align-items: center;
  justify-content: center; */
  max-width: 50%;
  max-height: 20%;
  width:auto;
  height:auto;
  margin-left: auto;
  margin-right: auto;
}

#icon {
  max-width:40%;
  max-height:15%;
}

ul {
  list-style:inside;
  list-style-type: "ðŸ§¸";
  margin-left: auto;
  margin-right: auto;
  padding-right: 0;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

ul li {
  font-size:x-large;
  position:relative;
  text-align:center;
  padding:3%;
}

.inner-li {
  transition: var(--transition);
}

.inner-li:hover{
  font-size:xx-large;
}

.nutrition_label{
  display: flex;
  max-width:80%;
  max-height:90%;
  width: auto;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.item {
  background: linear-gradient(215deg, rgba(0,0,0,1) 0%, rgba(88,84,73,1) 51%, rgba(166,127,3,1) 100%);
  display:block;
  /* justify-content: space-between; */
  justify-content:flex-start;
  max-width: var(--fixed-width);
  margin: 2rem auto;
  align-items:flex-start;
  border-radius: var(--radius);
  border-style:solid;
  border-width:0.7em;
  border-image: linear-gradient(90deg, rgba(255,252,6,1) 0%, rgba(255,187,0,1) 51%, rgba(186,147,23,1) 100%);
  border-image-slice: 1;
}

