button{
  font-family: 'Ubuntu', sans-serif;
  position: relative;
  /* top: 50%; */
  left:50%;
  transform: translate(-50%, 20%);
  
  width: 170px;
  height: 40px;
  line-height: 1;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1px;
  border: 3px solid black;
  background: #fff;
  color: black;
  border-radius: 40px;
  cursor: pointer;
  overflow: hidden;
  transition: all .35s;
}

button:hover{
  background: black;
  color: #fff;
}

